#flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top:55px;
    width: 75%;
  }
  
  #flex-container > .flex-item {
    flex: auto;
  }
  
  #flex-container > .raw-item {
    width: 5rem;
  }

  
  #flex-container > div {
    border: 1px solid #f00;
    padding: 1rem;
  }
  
  #flex-container > .raw-item {
    border: 1px solid #000;
  }
  
  .row{
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-bottom: 10px solid transparent;
  }
  .center {
      margin-top: 35px;
    margin: auto;
    width: 50%;
    padding: 10px;
  }